import { Component, OnInit } from '@angular/core';
import { SimuladorService } from 'src/services/simulador.service';
import { ActivatedRoute, ParamMap } from '@angular/router';
@Component({
  selector: 'app-etapa1',
  templateUrl: './etapa1.component.html',
  styleUrls: ['./etapa1.component.css']
})
export class Etapa1Component implements OnInit {

  tabela;
  tabelaActive = false;
  pergunta:any = 0;
  constructor(private service: SimuladorService, private route: ActivatedRoute) { }

  ngOnInit(): void {
    this.route.paramMap.subscribe((params: ParamMap)=>{
      this.pergunta = params.get("pergunta");
    })
    
    this.service.GetTabela(1).subscribe(res => {
      console.log(res);
      this.tabela = res;
      this.tabelaActive = true;
    }, error => {
      console.log(error);
    });
  }

  onNext(){
    if(this.pergunta == this.tabela.campos.length - 1){
      return '/etapa1/resultado'
    }
    else{
      return <string>'etapa1/'+(parseInt(this.pergunta)+1)
    }
  }

}
