  ConvertToDataURLviaCanvas(svgString: any) {
    // let outputFormat = (url.replace(/^.*[\\\/]/, '').split(".").length > 1) ? url.replace(/^.*[\\\/]/, '').split(".")[1] : "jpeg";
    // let newurl = (url.replace(/^.*[\\\/]/, '').split(".").length > 1) ? url.replace(/^.*[\\\/]/, '') : url.replace(/^.*[\\\/]/, '') + "." + outputFormat;
    return new Promise((resolve, reject) => {
      let img = new Image();
      let canvas = <HTMLCanvasElement>document.createElement('CANVAS'),
        ctx = canvas.getContext('2d'),
        svg = new Blob([svgString], { type: "image/svg+xml;charset=utf-8" }),
        url = window.URL.createObjectURL(svg);
      img.crossOrigin = 'Anonymous';
      img.onload = () => {
        let dataURL
        canvas.height = img.height;
        canvas.width = img.width;
        ctx.drawImage(img, 0, 0);

        dataURL = canvas.toDataURL("image/png");
        resolve(dataURL);
        canvas = null;
      };
      img.src = url;

    }).then((x: string) => this.Base64toBlob(x, "resultado.png", "image/png"));
  }

  Base64toBlob(dataURI: string, name: string, outputFormat: string) {
    var byteString = atob(dataURI.split(',')[1]);
    var ab = new ArrayBuffer(byteString.length);
    var ia = new Uint8Array(ab);

    for (var i = 0; i < byteString.length; i++) {
      ia[i] = byteString.charCodeAt(i);
    }
    return this.BlobToFile(new Blob([ab], { type: outputFormat }), name);
  }

  BlobToFile(blob: Blob, fileName: string): File {
    var b: any = blob;
    //A Blob() is almost a File() - it's just missing the two properties below which we will add
    b.lastModifiedDate = new Date();
    b.name = fileName;

    //Cast to a File() type
    return <File>blob;
  }




