import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { ResultadoComponent } from './resultado/resultado.component';
import { ResultadoChartComponent } from './resultado/resultado-chart/resultado-chart.component';
import { SimuladorComponent } from './simulador/simulador.component';
import { Etapa1Component } from './simulador/etapa1/etapa1.component';
import { LoginComponent } from './login/login.component';
import { RegistrationComponent } from './registration/registration.component';
import { AuthGuard } from './auth/auth.guard';
import { InstrucoesComponent } from './instrucoes/instrucoes.component';
import { Parte1Component } from './instrucoes/parte1/parte1.component';
import { Parte2Component } from './instrucoes/parte2/parte2.component';
import { SelecaoComponent } from './selecao/selecao.component';


const routes: Routes = [
  { path: '', redirectTo: 'login', pathMatch: 'full' },
  { path: "login", component: LoginComponent },
  { path: "register", component: RegistrationComponent },
  {
    path: "simulador", component: SimuladorComponent,
    children: [
      { path: "home", component: HomeComponent, canActivate: [AuthGuard] },
      {
        path: "instrucoes", component: InstrucoesComponent,
        children: [
          { path: "parte1", component: Parte1Component, canActivate: [AuthGuard] },
          { path: "parte2", component: Parte2Component, canActivate: [AuthGuard] }
        ],
        canActivate: [AuthGuard]
      },
      { path: "selecao", component: SelecaoComponent, canActivate: [AuthGuard] },
      { path: "etapa1/resultado", component: ResultadoChartComponent, canActivate: [AuthGuard] },
      { path: "etapa1/:pergunta", component: Etapa1Component, canActivate: [AuthGuard] }
    ]
  },

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
export const routingComponents = [
  HomeComponent,
  SimuladorComponent,
  Etapa1Component,
  ResultadoComponent,
  ResultadoChartComponent,
  LoginComponent,
  RegistrationComponent,
  InstrucoesComponent,
  SelecaoComponent,
  Parte1Component,
  Parte2Component
]
