import { Component, OnInit, Input } from '@angular/core';
import { SimuladorService } from 'src/services/simulador.service';
import { fas } from '@fortawesome/free-solid-svg-icons';
import { far } from '@fortawesome/free-regular-svg-icons';
import { FontAwesomeModule, FaIconLibrary } from '@fortawesome/angular-fontawesome';

@Component({
  selector: 'app-resposta',
  templateUrl: './campo-combobox.component.html',
  styleUrls: ['./campo-combobox.component.css']
})
export class CampoComboboxComponent implements OnInit {


  @Input() respostas;
  @Input() index;
  active
  usuario:any = {
    resposta:""
  }
  selectedEntry;

  constructor(private service : SimuladorService,library: FaIconLibrary) {
    library.addIconPacks(fas, far);
   }


  ngOnInit(): void {
    var valor = 100 / (this.respostas.subCampos.length -1);
    
    this.service.GetCampoUsuario(this.respostas.id).subscribe(res => {
      this.usuario = res;
      this.respostas.subCampos.forEach((element,idx) => {
        element.resposta = Math.round(valor * idx);    
        this.active = this.usuario.resposta; 
        if(parseInt(this.usuario.resposta) == element.resposta){
          // element.active = true;
        }
      });
      console.log(res);
    }, error => {
      console.log(error)
    });
  }



  getResposta(resposta){
    this.active = resposta;
    this.usuario.resposta = resposta;
    this.service.SetCampoUsuario(this.usuario).subscribe(res=>{
      console.log(res);
    },error=>{
      console.log(error);      
    });
  }

}
